import React, { useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { BiFilterAlt } from "react-icons/bi";

import { clearProfile } from "../../../../../../../../actions/user";
import { loadGradesAv, gradesPDF } from "../../../../../../../../actions/grade";

import ListButtons from "../../../sharedComp/ListButtons";

function AverageTab({
   grades: { grades: students, loading },
   categories: { categories },
   gradesPDF,
   loadGradesAv,
   clearProfile,
}) {
   const thisYear = new Date().getFullYear();
   const yearArray = new Array(3).fill().map((item, index) => thisYear - index);

   const [filterData, setFilterData] = useState({
      quantity: "",
      category: "",
      year: thisYear,
   });

   const [adminValues, setAdminValues] = useState({
      page: 0,
   });

   const { quantity, category, year } = filterData;

   const { page } = adminValues;

   const onChange = (e) => {
      e.persist();
      setFilterData({
         ...filterData,
         [e.target.name]: e.target.value,
      });
   };

   return (
      <>
         <form
            className="form"
            onSubmit={(e) => {
               e.preventDefault();
               setAdminValues((prev) => ({ ...prev, page: 0 }));
               loadGradesAv(filterData);
            }}
         >
            <div className="form-group">
               <div className="two-in-row">
                  <select
                     className="form-input"
                     id="category"
                     name="category"
                     onChange={onChange}
                     value={category}
                  >
                     <option value="">* Seleccione Categora</option>
                     {categories.map(
                        (category) =>
                           category.name !== "Inscripcin" && (
                              <option key={category._id} value={category._id}>
                                 {category.name}
                              </option>
                           )
                     )}
                  </select>
                  <select
                     className="form-input"
                     id="year"
                     name="year"
                     onChange={onChange}
                     value={year}
                  >
                     <option value="">* Seleccione el Ao</option>
                     {yearArray.map((item) => (
                        <option key={item} value={item}>
                           {item}
                        </option>
                     ))}
                  </select>
               </div>
               <div className="two-in-row">
                  <label
                     htmlFor="category"
                     className={`form-label ${category === "" ? "lbl" : ""}`}
                  >
                     Categora
                  </label>
                  <label
                     htmlFor="year"
                     className={`form-label ${year === "" ? "lbl" : ""}`}
                  >
                     Ao
                  </label>
               </div>
            </div>
            <div className="form-group">
               <input
                  className="form-input"
                  type="number"
                  value={quantity}
                  onChange={onChange}
                  name="quantity"
                  id="quantity"
                  placeholder="Cantidad"
               />
               <label htmlFor="quantity" className="form-label">
                  Cantidad
               </label>
            </div>
            <div className="btn-right mb-1">
               <button type="submit" className="btn btn-light">
                  <BiFilterAlt />
                  &nbsp;Buscar
               </button>
            </div>
         </form>
         <div className="wrapper mt-3">
            <table>
               <thead>
                  <tr>
                     <th>Legajo</th>
                     <th>Nombre</th>
                     <th>Categora</th>
                     <th>Promedio</th>
                  </tr>
               </thead>
               <tbody>
                  {!loading &&
                     students.map(
                        (item, i) =>
                           i >= page * 10 &&
                           i < (page + 1) * 10 && (
                              <tr key={i}>
                                 <td>{item.student.studentnumber}</td>
                                 <td>
                                    <Link
                                       className="btn-text"
                                       to={`/index/dashboard/${item.student._id}`}
                                       onClick={() => {
                                          window.scroll(0, 0);
                                          clearProfile();
                                       }}
                                    >
                                       {item.student.lastname +
                                          ", " +
                                          item.student.name}
                                    </Link>
                                 </td>
                                 <td>{item.category.name}</td>
                                 <td>{item.average}</td>
                              </tr>
                           )
                     )}
               </tbody>
            </table>
         </div>
         {!loading && (
            <ListButtons
               type="mejores promedios"
               page={page}
               items={students}
               pdfGenerator={() => gradesPDF(null, students, { year })}
               changePage={(page) =>
                  setAdminValues((prev) => ({ ...prev, page }))
               }
            />
         )}
      </>
   );
}

const mapStateToProps = (state) => ({
   grades: state.grades,
   categories: state.categories,
});

export default connect(mapStateToProps, {
   gradesPDF,
   loadGradesAv,
   clearProfile,
})(AverageTab);
